<template>
  <div>
    <h2>Переменные, декларативное и императивное программирование</h2>

    <h3>Транскрипт урока</h3>
    <p>
      Мы уже знакомы со способом называть что-то, используя константы. Например,
      это константа pi со значением 3.14.
    </p>
    <code>const pi = 3.14;</code>
    <p>
      После этой строки, каждый раз, когда мы видим pi, мы знаем, что её
      значение 3.14. Она называется константой, потому что, ээм, она неизменна,
      постоянна. После этой строки pi всегда будет 3.14, это никогда не
      изменится. Именно поэтому по аналогии с бумагой я использую ручку.
    </p>
    <p>
      Это может показаться ограничением, но вообще — это довольно хорошее
      свойство. Изменение того, что мы уже создали — сложная задача.
      Представьте, что пишете код, используя константу, в значении которой не
      уверены.
    </p>
    <p>
      Тем не менее, иногда вам может потребоваться изменить уже существующие
      данные или, другими словами, их значения. Допустим, вы хотите повторить
      что-то 5 раз. Один способ — выполнять повторы, отсчитывая до пяти, а затем
      остановиться. Для этого вам потребуется что-нибудь для хранения счётчика
      этого меняющегося числа. Константа не будет в таком случае работать — она
      неизменна, вы не можете изменить её значение после того, как уже создали
      её.
    </p>
    <p>
      Поэтому в JavaScript и многих других языках программирования существует
      идея переменной. Можно представить ее в виде такой же бумажки, на которой
      имя написано ручкой, но значение — карандашом. В любой момент можно
      заменить значение на другое.
    </p>
    <p>Можно посчитать факториал с помощью переменной вот так:</p>
    <code>
      let factorial = 1;<br />
      factorial = factorial * 2; // 2 <br />
      factorial = factorial * 3; // 6 <br />
      factorial = factorial * 4; // 24<br />
      factorial = factorial * 5; // 120<br
    /></code>

    <p>
      Создать переменную — просто, она выглядит как константа, только вместо
      const мы пишем let. Мы позволяем ей быть чем-то и это не навсегда.
    </p>
    <p>
      Затем мы изменяем значение factorial. Мы бы такого не смогли сделать, если
      бы factorial был константой. Эта строка означает "изменить значение
      переменной факториал, на результат умножения факториала на 2". Теперь
      JavaScript умножает factorial на 2 и хранит этот результат в переменной
      factorial. Раньше factorial был 1, а теперь это 2.
    </p>
    <p>
      Мы повторяем это ещё трижды, каждый раз умножая полученное значение на
      следующе целое число: на 3, на 4 и на 5. Это то, что вы возможно делаете в
      уме, когда умножаете числа. Вероятно вы не думали об этом так чётко, но
      это неплохой способ описания процесса вычисления, если бы вам было нужно
      объяснить его.
    </p>
    <p>
      Идея использования счётчика для повторения чего-то множество раз —
      распростанённая в программировании, и большинство языков программирования
      имеют для этого "циклы". Давайте рассмотрим один тип цикла — "цикл while".
      Это блок кода, который повторяется, пока удовлетворяется какое-то условие.
    </p>
    <p>
      Представьте фермера, который работает от рассвета до заката. Другими
      словами, он работает пока солнце в небе. Вы можете записать:
    </p>
    <code
      >while (sun is up) { <br />
      work<br />
      }<br
    /></code>

    <p>
      Конечно, это не настоящий JavaScript, это просто чтобы показать идею. Эта
      строка "work" будет повторяться снова и снова, пока солнце над горизонтом.
      Это значит после каждого повторения нам нужно проверять, действительно ли
      солнце в небе, и остановиться если это не так. Другими словами: проверить
      — исполнить, проверить — исполнить, и так далее.
    </p>
    <p>Вот функция факториала с переменными и циклом вместо рекурсии.</p>
    <code
      >const factorial = (n) => { <br />
      let counter = 1;<br />
      let result = 1;<br />

      while (counter &#60;= n) {<br />
      result = result * counter;<br />
      counter = counter + 1;<br />
      }<br />
      <br />
      return result;<br />
      }<br
    /></code>

    <p>
      О-о, что тут происходит? Во-первых, мы создали две переменные: одна для
      счётчика, чтобы считать от 1 до верхнего предела, а вторая для текущего
      результата.
    </p>
    <p>
      Затем начинается главная часть: цикл while, который повторяется, пока
      счётчик меньше или равен n — числу, переданному в эту функцию. Код,
      который повторяется, простой: мы меняем значения наших двух переменных.
      Текущий результат умножается на счётчик, а счётчик увеличивается на 1.
    </p>
    <p>
      В какой-то момент это условие — "счётчик меньше или равен n" — станет
      ложным, цикл больше не будет повторяться, а программа перейдёт к
      следующему этапу — return result. К этому моменту результат станет
      ответом, потому что за время всех повторов в цикле, результат умножался на
      1, затем на 2, 3 и так далее, пока не достиг значения n, каким бы оно ни
      было.
    </p>
    <p>
      Давайте посмотрим, что компьютер делает шаг за шагом, когда мы вызываем
      факториал 3.
    </p>
    <ol>
      <li>Взять один аргумент — 3, известный внутри, как n</li>
      <li>Создать переменную counter, установить значение 1</li>
      <li>Создать переменную result, установить значение 1</li>
      <li>Проверить: в счётчике — 1, это меньше или равно n, поэтому</li>
      <li>Умножить result на counter и положить ответ — 1 — в result</li>
      <li>Добавить 1 к counter и положить ответ — 2 — в counter</li>
      <li>
        Вернуться и проверить: counter — 2, это меньше или равно n, поэтому
      </li>
      <li>Умножить result на counter и положить ответ — 2 — в result</li>
      <li>Добавить 1 к counter и положить ответ — 3 — в counter</li>
      <li>
        Вернуться и проверить: counter — 3, это меньше или равно n, поэтому
      </li>
      <li>Умножить result на counter и положить ответ — 6 — в result</li>
      <li>Добавить 1 к counter и положить ответ — 4 — в counter</li>
      <li>
        Вернуться и проверить: counter — 4, это не меньше и не равно n, поэтому
        остановить повтор и перейти к следующей строке
      </li>
      <li>Вернуть result — 6</li>
    </ol>
    <p>
      Компьютер выполняет такие операции в миллиарды раз быстрее, но по сути это
      выглядит именно так. Общее название такого вида сформулированных
      повторений — "итерация". Наша программа использует итерацию, чтобы
      рассчитать факториал.
    </p>
    <p>
      В прошлый раз мы рассматривали итеративный процесс с рекурсией, а в этот —
      итеративный процесс без рекурсии.
    </p>
    <p>
      Оба используют технику итерации, но с рекурсивными вызовами нам не нужно
      менять значения, мы просто передаём новые значения в следующий вызов
      функции. А эта функция факториала не имеет рекурсивных вызовов вообще,
      поэтому все трансформации должны происходить внутри единственного
      экземпляра, единственной функциональной коробки. У нас нет выбора, кроме
      как менять значения содержимого.
    </p>
    <p>
      Стиль программирования, который вы видели в предыдущих уроках, называется
      "декларативным". Сегодняшний стиль, с изменением значений, называется
      "императивным".
    </p>
    <p>Сравните рекурсивный и нерекурсивный факториалы:</p>
    <code
      >const recursiveFactorial = (n) => { <br />
      if (n === 1) {<br />
      return 1;<br />
      }<br />
      return n * recursiveFactorial(n-1);<br />
      }<br />
      <br />
      const factorial = (n) => {<br />
      let counter = 1;<br />
      let result = 1;<br />

      while (counter &#60;= n) {<br />
      result = result * counter;<br />
      counter = counter + 1;<br />
      }<br />

      return result;<br />
      }<br
    /></code>

    <p>
      Эта рекурсивная функция — декларативная — она как описание факториала. Она
      объясняет, что такое факториал.
    </p>
    <p>
      Это нерекурсивная итеративная функция, и она императивная — она описывает,
      что делать, чтобы найти факториал.
    </p>
    <p>
      Слово декларативный происходит от латинского "clarare" — разъяснять,
      заявлять, делать объявление. Вы разъясняете: я хочу, чтобы факториал n был
      n умножить на факториал n-1.
    </p>
    <p>
      Слово императивный происходит от латинского "imperare", что значит
      "командовать". Вы приказываете чётко передвигаться по шагам — умножать это
      на это, пока идёт отсчёт и запоминать какие-то числа.
    </p>
    <p>Декларативное — это что. Императивное — это как.</p>
    <p>
      Писать декларативный код, в целом, лучший подход. Ваш код будет легче
      читать, понимать и делать что-то новое опираясь на него. Некоторые языки
      провоцируют вас использовать тот или иной подход, а некоторые вообще не
      оставляют выбора.
    </p>
    <p>
      Но в итоге вам нужно будет научиться оценивать, когда императивный подход
      принесет больше проблем чем решений.
    </p>
    <p>
      Следить за изменениями – сложно, и буквально несколько переменных могут
      сделать систему очень сложной для понимания.
    </p>
    <p>
      От изменения состояния* появляется гора багов, а оператор присваивания
      (assignment statements), который создает изменения, часто является
      причинами всего зла во вселенной.
    </p>
    <p class="author-rights">
      Материал взят из источника https://ru.hexlet.io/
    </p>
  </div>
</template>

